.home {
    padding: 0 100px 100px 100px;
}

.home-title {
    font-size: 25vw;
    margin-left: -4px;
    color: #eaeaea;
}

.home-resume::before {
    content: '';
    width: 100px;
    height: 2px;
    background: #eaeaea;
    position: absolute;
    top: 50%;
    left: -125px;
    cursor: default;
}

.home-resume {
    color: #eaeaea;
    text-decoration: none;
    font-size: 36px;
    position: relative;
    left: 125px;
    -webkit-transition: color 0.3s ease-in-out, padding 0.3s ease-in-out;
    -moz-transition: color 0.3s ease-in-out, padding 0.3s ease-in-out;
    -o-transition: color 0.3s ease-in-out, padding 0.3s ease-in-out;
    transition: color 0.3s ease-in-out, padding 0.3s ease-in-out;
}

.home-resume:hover {
    color: #b2b2b2;
    padding-left: 4px;
}

@media screen and (max-width: 836px) {
    .home-title {
        font-size: 22vw;
    }
}

@media screen and (max-width: 648px) {
    .home-resume::before {
        width: 75px;
        left: -100px;
    }
    .home-resume {
        font-size: 32px;
        left: 100px;
    }
}

@media screen and (max-width: 584px) {
    .home {
        padding: 0 75px 75px 75px;
    }
    .home-title {
        font-size: 20vw;
    }
    .home-resume::before {
        width: 50px;
        left: -75px;
    }
    .home-resume {
        font-size: 28px;
        left: 75px;
    }
}

@media screen and (max-width: 474px) {
    .home {
        padding: 0 50px 75px 50px;
    }
    .home-title {
        font-size: 22vw;
    }
}

@media screen and (max-width: 368px) {
    .home {
        padding: 0 25px 75px 25px;
    }
    .home-resume::before {
        width: 35px;
        left: -50px;
    }
    .home-resume {
        font-size: 24px;
        left: 50px;
    }
}

@media screen and (max-width: 300px) {
    .home {
        width: 300px !important;
        box-sizing: border-box;
    }
    .home-title {
        font-size: 66px;
    }
}