.header-buffer {
    position: relative;
    width: 100%;
    height: 150px;
    z-index: -999;
}

.header {
    position: absolute;
    width: 100%;
    height: 150px;
    padding: 0 100px;
    box-sizing: border-box;
}

.header > ul {
    position: relative;
    height: 100px;
    list-style-type: none;
}

.header-logo {
    position: absolute;
    bottom: 0;
    padding-bottom: 19px;
    color: #ffffff;
}

.header-tagline {
    position: absolute;
    bottom: 0;
    right: 125px;
    width: 318px;
    color: #ffffff;
}

.header-hamburger-container {
    width: 40px;
    height: 40px;
    position: absolute;
    right: 0;
    bottom: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.header-hamburger {
    width: 24px;
    height: 10px;
}

.nav {
    position: absolute;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    background: #eaeaea;
    z-index: 998;
    overflow: auto;
    box-sizing: border-box;
    padding: 0 100px 100px 100px;
    -webkit-transition: top 0.3s ease-in-out;
    -moz-transition: top 0.3s ease-in-out;
    -o-transition: top 0.3s ease-in-out;
    transition: top 0.3s ease-in-out;
}

.nav-header {
    position: relative;
    width: 100%;
    height: 150px;
}

.nav-header > ul {
    position: relative;
    height: 100px;
    list-style-type: none;
}

.nav-header-logo {
    position: absolute;
    bottom: 0;
    padding-bottom: 19px;
    color: #000000;
}

.nav-header-tagline {
    position: absolute;
    bottom: 0;
    right: 125px;
    width: 318px;
    color: #000000;
}

.nav-header-close-container {
    width: 40px;
    height: 40px;
    position: absolute;
    right: 0;
    bottom: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    z-index: 999;
}

.nav-header-close {
    width: 24px;
    height: 24px;
}

.nav-title {
    color: #000000;
    font-size: 136px;
    margin-left: -4px;
    height: 200px;
}

.nav-list {
    display: grid;
    align-items: center;
    height: 300px;
    list-style-type: none;
}

.nav-list-item {
    font-size: 32px;
}

.nav-list-item > a {
    text-decoration: none;
    color: #000000;
    -webkit-transition: color 0.3s ease-in-out;
    -moz-transition: color 0.3s ease-in-out;
    -o-transition: color 0.3s ease-in-out;
    transition: color 0.3s ease-in-out;
}

.nav-list-item > a:hover {
    color: #666666;
}

@media screen and (max-width: 836px) {
    .nav-header-tagline,
    .header-tagline {
        display: none;
    }
    .nav-title {
        font-size: 128px;
        margin-left: -3px;
        height: 196px;
    }
    .nav-list-item {
        font-size: 30px;
    }
}

@media screen and (max-width: 648px) {
    .nav-title {
        font-size: 120px;
        margin-left: -2px;
        height: 188px;
    }
    .nav-list-item {
        font-size: 28px;
    }
}

@media screen and (max-width: 584px) {
    .nav {
        padding: 0 75px 100px 75px;
    }
    .nav-list {
        height: 260px;
    }
    .nav-title {
        font-size: 100px;
        margin-left: 0;
        height: 164px;
    }
    .nav-list-item {
        font-size: 24px;
    }
    .header {
        padding: 0 75px;
    }
}

@media screen and (max-width: 474px) {
    .nav {
        padding: 0 50px 100px 50px;
    }
    .nav-list {
        height: 220px;
    }
    .nav-title {
        font-size: 84px;
        margin-left: 0;
        height: 132px;
    }
    .header {
        padding: 0 50px;
    }
}

@media screen and (max-width: 368px) {
    .nav {
        padding: 0 25px 100px 25px;
    }
    .header {
        padding: 0 25px;
    }
}

@media screen and (max-width: 300px) {
    body,
    .nav {
        min-width: 300px !important;
        width: 300px !important;
        overflow: auto;
    }
    .header {
        width: 300px !important;
    }
}